.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 10;
    background: rgba(0, 0, 0, 0.75);
  }
  
  .modal {
    padding: 0;
    position: fixed;
    top: 30vh;
    left: 10%;
    width: 10%;
    z-index: 100;
    overflow: hidden;
    background-color: white;
    text-align: center;
    
  }
  
  .header {
    background: #000000;
    padding: 1rem;
    width: 100%;
  }
  
  .header h2 {
    margin: 0;
    color: white;
  }
  
  .content {
    padding: 1rem;
  }
  
  .actions {
    width: 100%;
    padding: 1rem;
    display: flex;
    justify-content: flex-end;
  }

  .hidden #modal {
    display: none;
  }

  .visible {
    display: block;
  }
  
  @media (min-width: 768px) {
    .modal {
      left: calc(50% - 20rem);
      width: 40rem;
    }
  }